@import "breakpoint";

:root {
  --scale: 1;

  --text_titleH1: calc(6rem * var(--scale));
  --text_titleH2: 2.25rem;
  --text_titleH3: 1.5rem;

  --text_p_large_x: calc(5rem * var(--scale));
  --text_p_large_xl: calc(4.5rem * var(--scale));;
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  :root {
    --scale: .5;
  }
}
