* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  min-height: 100vh;
}

body {
  background: var(--color-background-black);
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  overflow-x: hidden;
}

.shade {
  position: absolute;
  z-index: -1;
}

.animation {
  overflow: hidden;
}
