.section {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: var(--gap-xx);
  width: 100%;
  overflow-x: hidden;

  .textContainer {
    display: flex;
    flex-direction: column;

    .text {
      font-size: var(--text_p_large_xl);
      color: white;
      font-weight: 500;
      margin-top: var(--gap-large);
      text-align: center;
      white-space: pre-line;
    }
  }
}
