@import "../../../styles/breakpoint";

.benefitsSection {
  width: 100%;

  .container {
    display: grid;
    grid-template-columns: 40% auto;
    gap: var(--gap-x);

    .textContainer {
      display: flex;
      flex-direction: column;
    }

    .description {
      padding: var(--gap-xx) 0 var(--gap-xx) var(--gap-xx);
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100vh;

      .title {
        font-size: var(--text_titleH3);
        color: var(--color-brand);
      }

      .body {
        margin: var(--gap-x) 0;
        width: 65%;
        line-height: 28px;
        color: white;
      }

      .companies {
        width: 75%;
      }
    }

    .image {
      height: 100%;

      .laptop {
        position: sticky;
        top: 0;
        height: 80vh;
        width: 100%;
        transform: translateX(20%);
      }
    }
  }
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  .benefitsSection {
    .container {
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr auto;
      grid-template-areas: 'image' 'text';

      .textContainer {
        grid-area: text;
        display: flex;
        flex-direction: column;
      }

      .description {
        padding: 0 var(--gap-xx);
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: initial;

        &:not(:last-child) {
          margin-bottom: var(--gap-xx);
        }

        .body {
          width: 100%;
        }

        .companies {
          width: 100%;
        }
      }

      .image {
        grid-area: image;
        height: 100%;

        .laptop {
          position: initial;
          transform: initial;
          height: 50vh;
          width: 100%;
          padding: var(--gap-x);
        }
      }
    }
  }
}
