@import "./src/styles/field";

.button {
  @extend %field;

  font-weight: 700;
  padding: 0 var(--gap-x);
  color: white;
  background: var(--color-brand);
  cursor: pointer;
  transition: .25s;
}

.button:hover, .button:focus {
  background: var(--color-brand);
  color: var(--color-brand-dark)
}
