@import "../../../styles/breakpoint";

.section {
  width: 100%;

  .readySection {
    display: flex;
    place-items: center;
    place-content: center;
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100vh;

    .readyTitle {
      font-size: var(--text_p_large_xl);
      color: white;
      font-weight: 900;
    }
  }

  .stepSection {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-image: url("../../../images/films/22.webp");
    background-position: center;
    background-size: cover;
    height: 100vh;
    width: 100%;

    .subtitle {
      margin-top: var(--gap-x);
    }
  }
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-small')){
  .section {
    .stepSection {
      padding: var(--gap-xx);
      text-align: center;

      .subtitle {
        text-align: center;
        margin-top: var(--gap-x);
      }
    }
  }
}
