.cinemaSection {
  width: 100%;
  position: relative;
  overflow: hidden;

  .filmTrack:not(:last-child) {
    margin-bottom: var(--gap-small);
  }

  .filmTrackDuration {
    transition: all .75s;
  }

  .filmContainer > *:not(:last-child) {
    margin-right: var(--gap-small);
  }

  .card {
    object-fit: cover;
    width: 375px;
    height: 200px;
    position: absolute;
    z-index: 20;
    border-radius: 0;
  }
}
