@import "../../../styles/breakpoint";

.section {
  padding: var(--gap-x) var(--gap-xx);
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  height: 100vh;
  width: 100%;

  .title {
    color: var(--color-brand);
    text-align: center;
  }

  .description {
    width: 30%;
    text-align: center;

    .descriptionTitle {
      margin-bottom: var(--gap-small);
    }
  }

  .films {
    margin-top: var(--gap-xx);
    display: grid;
    grid-gap: var(--gap-xx);
    grid-template-columns: 1fr 2fr 1fr;
    align-items: center;
    width: 100%;

    .mainImage {
      position: relative;
      width: 100%;

      .image {
        width: 100%;
        min-height: 200px;
      }
    }

    .shade {
      width: 50px;
      height: 50px;
      border-radius: 100%;
      box-shadow: 0 0 250px 100px var(--color-brand-dark-80);
      z-index: -1;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
    }

    .image {
      width: 100%;
      min-height: 200px;
      border-radius: var(--border-radius-s);
      object-fit: cover;
    }
  }
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  .section {
    padding: 0;
    height: initial;

    .title {
      padding: var(--gap-xx);
      text-align: left;
    }

    .description {
      width: 100%;
      padding: var(--gap-xx);
      text-align: left;

      .descriptionTitle {
        margin-bottom: var(--gap-small);
      }
    }

    .films {
      margin-top: var(--gap-xx);
      display: grid;
      grid-gap: var(--gap-small);
      grid-template-columns: 1fr 2fr 1fr;
      align-items: center;
      width: 100%;

      .mainImage {
        position: relative;
        width: 100%;

        .image {
          width: 100%;
          border-radius: var(--border-radius-s) !important;
          min-height: 250px;
        }
      }

      .shade {
        width: 50px;
        height: 50px;
        border-radius: 100%;
        box-shadow: 0 0 250px 100px var(--color-brand-dark-80);
        z-index: -1;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-right: -50%;
        transform: translate(-50%, -50%);
      }

      .image {
        &:nth-child(1) {
          border-radius: 0 var(--border-radius-s) var(--border-radius-s) 0;
        }

        &:nth-child(3) {
          border-radius: var(--border-radius-s) 0 0 var(--border-radius-s);
        }

        object-fit: cover;
      }
    }
  }
}
