%font {
  white-space: pre-line;
  color: white;
}

.highlight {
  font-weight: 900;
}

@each $titleType in H1, H2, H3 {
  .title#{$titleType} {
    @extend %font;
    font-size: var(--text_title#{$titleType});
  }
}

.textPLargeX {
  @extend %font;
  font-size: var(--text_p_large_x);
}

.textPLargeXL {
  @extend %font;
  font-size: var(--text_p_large_xl);
}

.textStandard {
  @extend %font;
  font-size: 1rem;
  line-height: 28px;
}
