@import "../../../styles/breakpoint";

.steps {
  overflow: hidden;
  width: 100%;
}

.step {
  height: 100vh;

  .secondStep {
    position: relative;

    .secondStepImage {
      transform: translateX(-120px);
    }
  }

  .stepImage {
    transform: translateX(40px);
  }

  .button {
    margin-top: var(--gap-xx);
  }
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  .step {
    height: initial;

    .secondStep {
      position: relative;

      .secondStepImage {
        height: 50vh;
        transform: translateX(-50%);
        width: 100%;
      }
    }

    .stepImage {
      height: 50vh;
      width: 100%;

      transform: translateX(0);
    }

    .button {
      margin-top: var(--gap-xx);
    }
  }
}
