.filmCard {
  width: 375px;
  height: 200px;
  background: #0E0E1A;
  overflow: hidden;
  border-radius: var(--border-radius-s);
  position: relative;

  .overlay {
    position: absolute;
    top: 0; bottom: 0;
    left: 0; right: 0;
    background: var(--color-brand-dark);
    opacity: .05;
    pointer-events: none;
    transition: .25s;

    &:hover {
      opacity: 0;
    }
  }

  .image {
    object-fit: cover;
    width: 100%;
    height: 100%;
    transition: 1s;

    &:hover {
      transform: translateX(-25px) scale(1.250);
    }
  }

  .logo {
    position: absolute;
    bottom: var(--gap-small); right: var(--gap-small);
    height: 15px;
  }
}
