@import "../../styles/breakpoint";

.footer {
  width: 100%;
  padding: var(--gap-xx);
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: inset 0 -80px 60px -60px rgba(9, 21, 90, .25);

  .support {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    font-weight: 500;

    .logo {
      height: 32px;
      margin-right: var(--gap-small);
    }
  }
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  .footer {
    flex-direction: column;

    & > *:not(:last-child) {
      margin-bottom: var(--gap-x);
    }
  }
}
