@import "../../styles/breakpoint";

.step {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;

  .container {
    padding: var(--gap-xx);

    .title {
      color: var(--color-brand);
    }

    .description {
      margin-top: var(--gap-x);
      width: 75%;
    }
  }

  .body {}
}

@media screen and (max-width: map-get($breakpoints-map, 'breakpoint-tablet-large')) {
  .step {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas: 'body' 'container';
    align-items: center;

    .container {
      grid-area: container;

      .description {
        width: 100%;
      }
    }

    .body {
      grid-area: body;
    }
  }
}
